@import "ScssHelpers/_helpers_all.scss";

.ecosystem_section {
	@include bp($point_2, min) {
		padding: 12rem var(--gl-indent) 0rem;
	}

	@include bp($point_3, $point_7) {
		padding: 2.1rem 7.2rem 0;
	}

	@include bp($point_7 - 1) {
		padding: 2.1rem 3rem 0;
	}
}

.ecosystem_section__title {
	@extend %h1;

	font-weight: 800;
	will-change: filter;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 6.4rem;
		}
		//.body--safari_state & {
		//	line-height: 1.55;
		//}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}
}
.ecosystem_section_desc {
	position: relative;
	overflow: visible;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}
}

.ecosystem_section_desc__decor,
.ecosystem_section_caption__decor {
	position: absolute;
	top: 0;
	left: 0;
	overflow: visible;

	background-color: $blue;

	@include bp($point_2, min) {
		width: 0.7rem;
		height: 2.7rem;
	}

	@include bp($point_3) {
		width: 0.5rem;
		height: 1.8rem;
	}
}

.ecosystem_section_desc__decor {
	@include bp($point_2, min) {
		margin-left: 3px;
		margin-top: 2px;
	}

	@include bp($point_3) {
		margin-top: 5px;
	}
}

.ecosystem_section_caption__decor {
	@include bp($point_2, min) {
		margin-left: 1px;
		margin-top: 2px;
	}

	@include bp($point_3) {
		margin-top: 5px;
	}

	.body--safari_state & {
		margin-top: 0;
	}
}

.ecosystem_section_desc__text {
	@extend %text_strong;

	@include bp($point_2, min) {
		padding-left: 4.6rem;

		//.body--safari_state & {
		//	line-height: 1.55;
		//}
	}

	@include bp($point_3) {
		text-indent: 3rem;
	}
}

.ecosystem_section__text {
	p {
		font-weight: 300;
		letter-spacing: 0.023rem;
		will-change: transform;

		strong {
			display: block;

			font-weight: 800;
		}

		@include bp($point_2, min) {
			font-size: 2.3rem;
			line-height: 1.46;

			&:not(:last-child) {
				margin-bottom: 3rem;
			}
		}

		@include bp($point_3) {
			font-size: 2.1rem;
			line-height: 1.33;

			&:not(:last-child) {
				margin-bottom: 2.8rem;
			}
		}
	}

	@include bp($point_2, min) {
		padding-left: 4.6rem;

		&:not(:last-child) {
			margin-bottom: 7.6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 5.4rem;
		}
	}
}

.ecosystem_section_caption {
	position: relative;

	@include bp($point_2, min) {
		padding: 14.2rem 2rem 14.2rem 4.6rem;
	}
	@include bp($point_3, $point_7) {
		padding: 5.5rem 7.1rem 5.9rem 7.3rem;
		margin: 0 -7.2rem;
	}

	@include bp($point_7) {
		padding: 3rem;
		margin: 0 -3rem;
	}
}

.ecosystem_section_caption__title {
	@extend %text_strong;

	position: relative;
	z-index: 2;

	color: $white;
	display: block;

	@include bp($point_2, min) {
		padding-left: 4.9rem;

		&:not(:last-child) {
			margin-bottom: 3.3rem;
		}

		//.body--safari_state & {
		//	line-height: 1.55;
		//}
	}
	@include bp($point_3) {
		text-indent: 3rem;
		line-height: 1.25;

		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}
}
.ecosystem_section_caption__slogan {
	@extend %text_strong;

	position: relative;

	color: $white;
	font-weight: 300;

	@include bp($point_2, min) {
		padding-left: 4.9rem;
	}

	@include bp($point_3) {
		font-size: 2.1rem;
	}
}

.ecosystem_section_caption__bg {
	position: absolute;
	top: 0;
	left: 0;

	height: 100%;
	width: 100vw;

	overflow: hidden;

	will-change: transform;
}

.ecosystem_section_caption__bg_in {
	width: 100%;
	height: 100%;
	background-color: $black_2;
	will-change: transform;
}

.ecosystem_section_caption__svg {
	object-fit: contain;
	width: 100%;
	height: 100%;

	object-position: left;
	will-change: opacity, transform;

	&.ecosystem_section_caption__svg--v2 {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
	}
}

.ecosystem_section_caption__img {
	@extend %contain_img;

	object-position: left;
}

.ecosystem__bg_img {
	@extend %contain_img;

	position: absolute;
	left: 0;
	top: 0;
	object-position: left;
}

.ecosystem_section_caption__img--1_mod {
	@include bp($point_4 - 1) {
		display: none;
	}
}

.ecosystem_section_caption__img--2_mod {
	width: 100%;
	height: 100%;

	@include bp($point_4, min) {
		display: none;
	}
}
