@import 'ScssHelpers/helpers_all';

.solutions {
	@include bp($point_2, min) {
		padding: 10.8rem 13.6rem 7rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 0;
	}
}

.solutions__list {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	@include bp($point_2, min) {
		margin: 0 -1.4rem -2rem 0;
	}
}

.solutions__list_item {
	@include bp($point_2, min) {
		width: 33.333%;
		padding: 0 1.4rem 2rem 0;
		//pointer-events: none;
	}

	@include bp($point_3) {
		position: relative;
		width: 100%;

		overflow: hidden;
	}

	&:nth-child(1) {
		& .solutions__list_item_decor--v1_mod {
			& .solutions__list_item_decor_in {
				background-image: linear-gradient(252deg, #A9BE74 0% 0%, #FFCA67 100% 100%);
			}
		}

		& .solutions__list_item_decor--v2_mod {
			& .solutions__list_item_decor_in {
				background-image: linear-gradient(50deg, #6FB783 0% 0%, #9EBD78 100% 100%);
			}
		}

		.solution_v2_decors__wrap {
			order: 2;
		}

		@include bp($point_3) {
			order: 3;
		}
	}

	&:nth-child(2) {
		& .solutions__list_item_decor--v1_mod {
			& .solutions__list_item_decor_in {
				background-image: linear-gradient(300deg, #AABE75 0% 0%, #FEC556 100% 100%);
			}
		}

		& .solutions__list_item_decor--v2_mod {
			& .solutions__list_item_decor_in {
				background-image: linear-gradient(108deg, #CCC46C 0% 0%, #FFCA67 56.44% 56.44%, #CBA353 100% 100%);
			}
		}

		.solution_v2_decors__item_in--v1_mod {
			background-image: linear-gradient(300deg, #AABE75 0% 0%, #FEC556 100% 100%);
		}

		.solution_v2_decors__item_in--v2_mod {
			background-image: linear-gradient(108deg, #CCC46C 0% 0%, #FFCA67 56.44% 56.44%, #CBA353 100% 100%);
		}

		.solution_v2_decors__wrap {
			padding-bottom: 1.4rem;
		}


		@include bp($point_3) {
			order: 1;
		}
	}

	&:last-child {
		& .solutions__list_item_decor--v1_mod {
			& .solutions__list_item_decor_in {
				background-color: #152B38;
			}
		}

		& .solutions__list_item_decor--v2_mod {
			& .solutions__list_item_decor_in {
				background-image: linear-gradient(250deg, #122430 0% 0%, #7B6E48 100% 100%);
			}
		}

		.solution_v2_decors__wrap {
			order: 2;
		}

		@include bp($point_3) {
			order: 2;
		}
	}
}

.solutions__list_item_in {
	@extend %transition_background;
	//overflow: hidden;

	@include bp($point_2, min) {
		border-radius: .8rem;
		background-image: linear-gradient(180deg, $white_2 0% 0%, $white_4 100% 100%);

		.solutions__list_item:nth-child(1).solutions__list_item_state & {
			background-color: $green;
			background-image: none;
		}

		.solutions__list_item:nth-child(2).solutions__list_item_state & {
			background-color: $yellow;
			background-image: none;
		}

		.solutions__list_item:nth-child(3).solutions__list_item_state & {
			background-color: $blue_2;
			background-image: none;
		}
	}

	@include bp($point_3) {
		position: relative;
		min-height: 56.3rem;
		padding: 5.6rem 7.3rem;

		@include flex_block(column, wrap, flex-start, flex-start, flex-start);


		.solutions__list_item:nth-child(1) & {
			background-color: $green;
		}

		.solutions__list_item:nth-child(2) & {
			background-color: $yellow;
		}

		.solutions__list_item:nth-child(3) & {
			background-color: $blue_2;
		}
	}
}

//-------------------------------------------- ITEM TOP
.solutions__list_item_top {
	display: block;

	@include bp($point_2, min) {
		min-height: 30rem;
		padding: 5rem 3.4rem 3rem 5rem;

		color: $black;
	}

	@include bp($point_3) {
		position: relative;

		flex-grow: 1;

		width: 100%;
		max-width: 40rem;

		order: 2;

		@include flex_block(column, wrap, flex-start, flex-start, flex-start);


		&::before {
			position: absolute;
			top: -6rem;
			left: -7rem;

			width: 26.6rem;
			height: 26.6rem;
			content: '';

			background-color: #5a5a5a;
			border-radius: 50%;
			opacity: .1;
		}
	}
	.solutions__list_item.solutions__list_item_state & {
		text-decoration: none;
	}
	.solutions__list_item.solutions__list_item_state:not(.solutions__list_item:nth-child(2)) & {
		color: $white;
	}

	&:focus,
	&:hover {
		text-decoration: none;
	}
}

.solutions__list_item_title {
	font-size: 3rem;
	font-weight: 800;
	letter-spacing: .012em;

	@include bp($point_2, min) {
		margin-bottom: 2rem;
	}

	@include bp($point_3) {
		margin-bottom: -.6rem;

		@include solutions_item_color;
	}
}

.solutions__list_item_descr {
	font-size: 1.8rem;
	font-weight: 300;
	letter-spacing: .012em;

	@include bp($point_2, min) {
		margin-bottom: 1rem;
		line-height: 1.4;
	}

	@include bp($point_3) {
		margin-bottom: 10rem;
		line-height: 1.5;

		@include solutions_item_color;
	}
}

.solutions__list_item_cta {
	@include bp($point_3) {
		@include flex_block(row, wrap, flex-start, center, flex-start);
		margin-top: auto;
	}
}

.solutions__list_item_cta_text {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		margin-right: 1rem;

		@include solutions_item_color;
	}
}

.solutions__list_item_arrow {
	width: 2.8rem;
	height: 1.2rem;

	@include bp($point_2, min) {
		.solutions__list_item.solutions__list_item_state:not(.solutions__list_item:nth-child(2)) & {
			color: $white;
		}
	}

	@include bp($point_3) {
		width: 2.5rem;
		height: 2.5rem;

		padding: 0.5rem;

		border: 1px solid $white;
		border-radius: 50%;

		@include solutions_item_color;

		.solutions__list_item:nth-child(2) & {
			border-color: $black;
		}
	}
}

.solutions__list_item_arrow_in {
	height: 100%;

	@include bp($point_3) {
		position: relative;
		overflow: hidden;

		& .icon_arrow_right {
			position: absolute;
			top: -48%;
			right: 0;

			width: 200%;
			height: 200%;
		}
	}
}

//-------------------------------------------- ITEM BOTTOM
.solutions__list_item_bottom {
	@include flex_block(column, wrap, flex-start, flex-start, flex-start);

	@include bp($point_2, min) {
		position: relative;
		padding-bottom: 2rem;
	}

	@include bp($point_3) {
		width: 100%;
		min-height: 4.5rem;
		margin-bottom: 10.8rem;
		order: 1;
	}
}

.solutions__list_item_decors {
	width: 100%;
	height: 100%;

	@include bp($point_3) {
		display: none;
	}

	.solutions__list_item:nth-child(1) &,
	.solutions__list_item:nth-child(3) & {
		order: 2;
		transform: rotate(90deg);
	}

	.solutions__list_item:nth-child(2) & {
		margin-bottom: 1rem;
	}
}

.solutions__list_item_decor {
	@extend %solutions_decor;

	opacity: .6;

	overflow: hidden;

	&.solutions__list_item_decor--v1_mod {
		transform: rotate(180deg);
	}

	@include bp($point_2, min) {
		&:hover {
			filter: blur(0);
			opacity: 1;
		}
	}
}

.solutions__list_item_decor_in {
	@extend %solutions_decor_in;
	position: absolute;
	top: 0;
	left: 0;
}

.solutions__list_item_logo {
	display: block;

	@include bp($point_2, min) {
		width: 100%;
	}

	@include bp($point_3) {
		width: 19.36rem;
	}

	.solutions__list_item:nth-child(1) &,
	.solutions__list_item:nth-child(3) &  {
		order: 1;

		@include bp($point_2, min) {
			padding-bottom: 1.4rem;
		}
	}
}

.solutions__list_item_logo_in {
	position: relative;


	@include bp($point_2, min) {
		padding-top: 100%;
		border-radius: 50%;
	}

	.solutions__list_item:nth-child(1) & {
		background-color: $green;
	}

	.solutions__list_item:nth-child(2) & {
		background-color: $yellow;
	}

	.solutions__list_item:nth-child(3) & {
		background-color: $blue_2;
	}
}

.solutions__list_item_logo_img_w {
	display: block;

	@include bp($point_2, min) {
		position: absolute;
		z-index: 2;
		top: 0;
		left: 50%;

		width: 62%;
		height: 100%;

		transform: translate(-50%, 0);

		.solutions__list_item:nth-child(3) & {
			width: 57%;
		}
	}
}

.solutions__list_item_logo_img {
	@extend %contain_img;
}

.solutions__list_item_decor_img_w {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.solutions__list_item_social_list {
	position: absolute;
	z-index: 2;

	@include bp($point_2, min) {
		right: 1.6rem;

		.solutions__list_item:nth-child(1) &,
		.solutions__list_item:nth-child(3) & {
			bottom: 1rem;
		}

		.solutions__list_item:nth-child(2) & {
			@include flex_block(row, wrap, flex-start, flex-start, flex-start);

			top: 4rem;
		}
	}

	@include bp($point_3) {
		bottom: 4.6rem;
		right: 2rem;

		@include flex_block(column-reverse, wrap, flex-start, flex-start, flex-start);
	}
}

.solutions__list_item_social_item {
	@include bp($point_2, min) {
		margin-bottom: 1rem;

		.solutions__list_item:nth-child(2) & {
			&:not(:last-child) {
				margin-right: .8rem;
			}
		}
	}

	@include bp($point_3) {
		margin-bottom: 1.4rem;
	}
}

.solutions__list_item_social_link {
	@extend %transition_background;
	@extend %global_flex_block_row_wrap_center_center;

	width: 2.5rem;
	height: 2.5rem;

	text-decoration: none;
	color: $white;

	border: 1px solid $white;
	border-radius: 50%;
	background-clip: padding-box;

	&:hover,
	&:focus {
		color: $black;
		background-color: $white;
	}
}

.solutions__list_item_social_link_in {
	width: 45%;
	height: 45%;
}

//----------------------------------- SIDE DECORS

.solutions__list_item_side_decors {
	//width: 100%;

	.solutions__list_item:nth-child(2) & {
		display: none;
	}

	@include bp($point_3) {
		display: none;
	}

}

.solutions__list_item_left_side_decors {
	position: absolute;
	top: 0;
	left: -102%;

	width: 97%;
	height: 100%;
	//pointer-events: none;

	.solutions__list_item:nth-child(3) & {
		display: none;
	}

	& .solutions__list_item_side_decor_w--v1_mod {
		left: -50%;
		transform: rotate(270deg);
		transform-origin: 100% 0;

		& .solutions__list_item_side_decor_in {
			background-color: $green;
		}
	}

	& .solutions__list_item_side_decor_w--v2_mod {
		top: 51%;
		transform: rotate(180deg);

		& .solutions__list_item_side_decor_in {
			background-image: linear-gradient(229deg, #45A78F 0% 0%, #67BD84 100% 100%);
		}
	}

	& .solutions__list_item_side_decor_w--v3_mod {
		top: 76%;

		& .solutions__list_item_side_decor_in {
			background-image: linear-gradient(223deg, #45A78F 0% 0%, #58B388 100% 100%);
		}
	}

	& .solutions__list_item_side_decor_w--v4_mod {
		left: -50%;
		transform: rotate(-270deg);
		transform-origin: 50% 100%;

		& .solutions__list_item_side_decor_in {
			background-color: $green;
		}
	}
}

.solutions__list_item_side_decor_w {
	@extend %solutions_decor;

	position: absolute;
	//filter: blur(10px);

	-webkit-filter: url(#f1);
	filter: url(#f1);
}

.solutions__list_item_side_decor {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	opacity: .6;
	overflow: hidden;
}

.solutions__list_item_side_decor_in {
	@extend %solutions_decor_in;
}

.solutions__list_item_right_side_decors {
	//pointer-events: none;
	position: absolute;
	top: 0;
	right: -102%;

	width: 98%;
	height: 100%;

	.solutions__list_item:nth-child(1) & {
		display: none;
	}

	& .solutions__list_item_side_decor_w--v1_mod {
		left: -50%;
		transform: rotate(90deg);
		transform-origin: 50% 100%;
	}

	& .solutions__list_item_side_decor_w--v2_mod {
		top: 51%;
		transform: rotate(180deg);
	}

	& .solutions__list_item_side_decor_w--v3_mod {
		top: 76%;
	}

	& .solutions__list_item_side_decor_w--v4_mod {
		left: 50%;
		transform: rotate(-90deg);
		transform-origin: 50% 100%;
	}

	& .solutions__list_item_side_decor_in {
		background-color: #152B38;
	}
}

//-------------------------------------------- MOB DECORS
.solutions__list_item_mob_decors {
	pointer-events: none;
	@include bp($point_2, min) {
		display: none
	}

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		filter: url(#f2);
	}
}

.solutions__list_item_mob_decors_img {
	@extend %contain_img;
	display: none;
}
.solutions__list_item_mob_decor {
	position: absolute;

	width: 26rem;
	height: 13rem;

	overflow: hidden;
	opacity: .14;

	&.solutions__list_item_mob_decor--v1_mod {
		top: -13rem;
		right: 0;

		transform: rotate(90deg);
		transform-origin: 50% 100%;

		.solutions__list_item:nth-child(1) & {
			top: -13rem;
			right: -13rem;
			transform: rotate(-90deg);
		}

		.solutions__list_item:nth-child(3) & {
			right: -13rem;
			transform: rotate(-90deg);
		}
	}

	&.solutions__list_item_mob_decor--v2_mod {
		top: 2rem;
		right: -13rem;

		transform: rotate(180deg);
		transform-origin: 50% 100%;

		.solutions__list_item:nth-child(3) & {
			top: 15rem;
			right: 0;
			transform: rotate(90deg);
		}
	}

	&.solutions__list_item_mob_decor--v3_mod {
		top: 28.5rem;
		right: -13rem;

		.solutions__list_item:nth-child(3) & {
			display: none;
		}
	}

	&.solutions__list_item_mob_decor--v4_mod {
		bottom: 0;
		right: -13.2rem;

		.solutions__list_item:nth-child(1) & {
			display: none;
		}
	}

	&.solutions__list_item_mob_decor--v5_mod {
		bottom: 0;
		left: 13rem;

		transform: rotate(-90deg);
		transform-origin: 50% 100%;
	}

	&.solutions__list_item_mob_decor--v6_mod {
		bottom: 0;
		left: -14rem;

		transform: rotate(90deg);
		transform-origin: 50% 100%;
	}
}

.solutions__list_item_mob_decor_in {
	@extend %solutions_decor_in;
	background-color: #5A5A5A;
}
/*=================Decor================*/
.solutions_decors_filter {
	position: absolute;
}

.solution_v2_decors__wrap {
	width: 100%;

	&.solution_v2_decors__wrap--rotate {
		transform: rotate(90deg);
	}

	@include bp($point_3) {
		display: none;
	}
}

.solution_v2_decors__item {
	position: relative;
	width: 100%;
	padding-top: 50%;
}

.solution_v2_decors__item_in {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	padding-top: 50%;
	width: 100%;
	height: 100%;

	opacity: .5;
	transition: filter .3s, opacity .3s;

	&:not(.solution_v2_decors__wrap--rotate &) {
		-webkit-filter: url(#f1);
		filter: url(#f1);

		&:hover {
			-webkit-filter: none;
			filter: none;
		}
	}

	.solution_v2_decors__wrap--rotate & {
		-webkit-filter: url(#f2);
		filter: url(#f2);

		&:hover {
			-webkit-filter: none;
			filter: none;
		}
	}

	&:hover {
		opacity: 1;
	}

	&.solution_v2_decors__item_in--v1_mod {
		border-radius: 0 0 50% 50% /0 0 100% 100%;

		.solution_v2_decors__wrap_v1_mod & {
			background-color: #152B38;
		}

		.solution_v2_decors__wrap_v2_mod & {
			background-image: linear-gradient(252deg, #A9BE74 0% 0%, #FFCA67 100% 100%);
		}
	}

	&.solution_v2_decors__item_in--v2_mod {
		border-radius: 50% 50% 0 0/100% 100% 0 0;

		.solution_v2_decors__wrap_v1_mod & {
			background-image: linear-gradient(250deg, #122430 0% 0%, #7B6E48 100% 100%);
		}

		.solution_v2_decors__wrap_v2_mod & {
			background-image: linear-gradient(50deg, #6FB783 0% 0%, #9EBD78 100% 100%);
		}
	}

	.solutions_v2_decors_side__right & {
		background-color: #152B38;
	}
}

.solutions_v2_decors_side__right,
.solutions_v2_decors_side__left {
	position: absolute;
	bottom: 2rem;
	width: 100%;
}

.solutions_v2_decors_side__right {
	right: -105%;
}

.solutions_v2_decors_side__left {
	left: -105%;

	.solutions_v2_decors_side__left_row:nth-child(1) {
		.solution_v2_decors__item_in {
			background-color: $green;
		}
	}

	.solutions_v2_decors_side__left_row:nth-child(2) {
		.solution_v2_decors__item_in--v1_mod {
			background-image: linear-gradient(229deg, #45A78F 0% 0%, #67BD84 100% 100%);
		}

		.solution_v2_decors__item_in--v2_mod {
			background-image: linear-gradient(223deg, #45A78F 0% 0%, #58B388 100% 100%);
		}
	}
}

.solutions_v2_decors_side__right_row,
.solutions_v2_decors_side__left_row {
	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

